@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&display=swap);
body {
  margin: 0;
  font-family: Merriweather Sans,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bar {
  padding-top: 7px;
  /* font-family: sans-serif; */
  font-weight: 700;
  display: inline-block;
  color: white;
  width: 40px;
  margin-left: 6px;
  background-color: #5e39d8;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.bar-sorted {
  background-color: #b979ec;
}
.bar-swap {
  background-color: #57a846;
}
.bar-min {
  background-color: red;
}

.heading {
  font-size: 30px;
  font-weight: 700;
  color: aliceblue;
  margin-bottom: 0px;
}
.bars {
