@font-face {
  font-family: Anurati;
  src: url(/Sorting-Visualizer/static/media/Anurati-Regular.ca8632f4.otf);
}
:root {
  /* --home-color: #2ed327; */
  --home-color: #f54242;
}
.algo-name {
  font-size: 18px;
  font-weight: 700;
  color: white;
}
.card {
  background-color: transparent;
}
.card::after {
  content: "";
  bottom: 0;
  top: 0;
  left: 0;
  position: absolute;
  height: 50%;
  width: 5px;
  color: #fff;
}
.algo-image {
  height: 175px;
  width: auto;
  object-fit: cover;
  fill: #f54242;
  fill: var(--home-color);
  background-color: transparent;
}
.back {
  background-color: #0e0e0e;
  min-height: 100vh;
}
#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: transparent;
  background-image: url("");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 0;
  top: 0;
  left: 0;
}

.logo {
  /* left: 50%;                         
    transform: translate( -50%,0); */
  position: relative;
  z-index: 100;
  float: top;
}
.links li {
  cursor: pointer;
  margin: 10px;
  fill: #f54242 !important;
  fill: var(--home-color) !important;
  margin-top: 25px;
  /* display: inline; */
}
.links li svg {
  height: 20px;
  width: auto;
}
.links {
  position: -webkit-sticky;
  position: sticky;
  z-index: 100;
  display: flex;
  list-style: none;
  vertical-align: top;
  float: left;
  cursor: pointer;
  /* position: absolute; */
  /* display: flex;
   flex-direction: row; */
  /* Below sets up your display method: flex-start|flex-end|space-between|space-around */
  justify-content: flex-start;
  /* Below removes bullets and cleans white-space */
  list-style: none;
  padding: 0;
  /* Bonus: forces no word-wrap */
  white-space: nowrap;
}
.main-title {
  font-size: 3rem;
  color: white;
  padding-top: 18vh;
  font-family: Anurati, sans-serif;
  letter-spacing: 5px;
}

.main-title svg {
  fill: #f54242;
  fill: var(--home-color);
  margin-bottom: 30px;
  height: 80px;
  width: auto;
}
.description-title {
  color: white;
  width: 50%;
  margin: auto;
}

td {
    border: 1px solid #5bc9b1;
    width: 25px;
    height: 25px;
}
  tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}
table{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    z-index: -1;
}
.node_start{
    background: url(/Sorting-Visualizer/static/media/space-ship.4db25bca.svg);
    background-size: cover;
}
.node_end{
    background: url(/Sorting-Visualizer/static/media/planet.2f3f3bdb.svg);
    background-size: cover;
}
.node_wall{
  /* background: url("../assets/asteroid.svg"); */
    -webkit-animation: pulse 0.5s 1;
            animation: pulse 0.5s 1;
    background-color: #202220;
    border: 1px solid #202220;
}

@-webkit-keyframes pulse{
    0%{
      background-color: grey;

    }
    100%{
      background-color: #202220;
    }
}

@keyframes pulse{
    0%{
      background-color: grey;

    }
    100%{
      background-color: #202220;
    }
}
.node_visited{
  /* background-color: lightseagreen; */
  background-color: rgba(4, 0, 255, 0.5);
  -webkit-animation-name: visited;
          animation-name: visited;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
@-webkit-keyframes visited{
  0% {
    transform: scale(0.3);
    background-color: rgba(255, 0, 191, 0.5);
    border-radius: 100%;
  }

  50% {
    background-color:  rgba(255, 0, 191, 0.5);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(4, 0, 255, 0.5);
  }

  100% {
    transform: scale(1);
    background-color:rgba(4, 0, 255, 0.5);
  }
}
@keyframes visited{
  0% {
    transform: scale(0.3);
    background-color: rgba(255, 0, 191, 0.5);
    border-radius: 100%;
  }

  50% {
    background-color:  rgba(255, 0, 191, 0.5);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(4, 0, 255, 0.5);
  }

  100% {
    transform: scale(1);
    background-color:rgba(4, 0, 255, 0.5);
  }
}
.node_path{
  background-color: greenyellow;
  -webkit-animation-name: path_anime;
          animation-name: path_anime;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  /* animation-iteration-count: infinite; */
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes path_anime{
  0% {
    transform: scale(0.3);
    background-color: red;
    border-radius: 100%;
}
50% {
    background-color: orange;
}
100% {
    transform: scale(1);
    background-color: yellow;
}
}

@keyframes path_anime{
  0% {
    transform: scale(0.3);
    background-color: red;
    border-radius: 100%;
}
50% {
    background-color: orange;
}
100% {
    transform: scale(1);
    background-color: yellow;
}
}
.chat-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.chat-btn {
  height: 50px;
  width: 50px;
  text-align: center;
  font-size: 2rem;
  color: #fff;
  background-color: #343A40;
  border: 1px solid #343A40;
  border-radius: 25px 25px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  
  display: none;
}
.chat-btn:focus{
  -webkit-animation: shadow-pulse 1s infinite;
          animation: shadow-pulse 1s infinite;
  outline:none;
}
@-webkit-keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0 #343A40;
  }
  100% {
    box-shadow: 0 0 0 20px rgba(130, 90, 164, 0);
  }
}
@keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0 #343A40;
  }
  100% {
    box-shadow: 0 0 0 20px rgba(130, 90, 164, 0);
  }
}
.chat-body {
  height: auto;
  width: auto;
  min-width: 10vw;
  /* min-height: 300px;
  min-width: 300px; */
  background:#343A40;
  position: absolute;
  bottom: 60px;
  right: 60px;
  box-shadow: 4px 4px 4px gray;
  border-radius: 8px;
  color: black;
  padding: 8px;
  margin-left: 10px;
}
.chat-body .chat-head {
  background: #343A40;
  padding: 10px;
  height: 20px;
  font-family: verdana;
}
.progress2 {
  border-radius: 30px;
  background-color: gray;
}

.progress-bar2 {
  height: 5px;
  border-radius: 30px;
  transition: 0.4s linear;
  transition-property: width, background-color;
}

.progress-moved .progress-bar2 {
  background-color: #f3c623;
  -webkit-animation: progress 5s 1;
          animation: progress 5s 1;
}
@-webkit-keyframes progress {
  0% {
    width: 0%;
    background: #f9bcca;
  }

  60% {
    /* width: 60%; */
    background: #f3c623;
    box-shadow: 0 0 40px #f3c623;
  }
}
@keyframes progress {
  0% {
    width: 0%;
    background: #f9bcca;
  }

  60% {
    /* width: 60%; */
    background: #f3c623;
    box-shadow: 0 0 40px #f3c623;
  }
}
.progress-text{
  color: black;
}
.progress-text:hover .comment{
  display: block;
}
.progress-text:hover .span-text{
  display: none;
}
.comment{
  display: none;
}
.info-title{
  font-size: 2.125rem;
    font-weight: 400;
    line-height: 1.235;
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #888;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1000;
    border-radius: 5px;
  }
  
  .modal-main {
    position:fixed;
    padding: 10px;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    border-radius: 5px;
    font-size: 20px;
  }
  
  .display-block {
      display: block;
  }
  
  .display-none {
    display: none;
  }
.modalImage{
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
}

